import { Component, OnInit } from '@angular/core'
import { VendorManufactureService } from 'src/app/sdk/asset/services'
import { NgxSpinnerService } from 'ngx-spinner'
import { FormBuilderTypeSafe, FormGroupTypeSafe } from 'src/app/services/angular-reactive-forms-helper'
import { PesanService } from 'src/app/services/tool/pesan.service'
import { Router, ActivatedRoute } from '@angular/router'
import { Manufacture } from 'src/app/sdk/asset/models'
import { FormControl, Validators } from '@angular/forms'

@Component({
    selector: 'app-edit',
    templateUrl: './edit.component.html',
    styleUrls: ['./edit.component.scss'],
})
export class EditComponent implements OnInit {
    constructor(
        private vendorManufactureService: VendorManufactureService,
        private spinner: NgxSpinnerService,
        private fb: FormBuilderTypeSafe,
        private pesan: PesanService,
        private router: Router,
        private activatedRoute: ActivatedRoute,
    ) { }

    data: Manufacture

    id_manufacture: string = ''

    ngOnInit() {
        this.id_manufacture = this.activatedRoute.snapshot.paramMap.get('id_manufacture')
        this.form()
        this.getData()
    }

    getData() {
        this.spinner.show()
        this.vendorManufactureService.getVendorManufacturesId(this.id_manufacture).subscribe(
            data=> {
                this.dataForm.setValue(data.data)
                this.spinner.hide()
            }
        )
    }

    dataForm: FormGroupTypeSafe<Manufacture>
    form() {
        this.dataForm = this.fb.group<Manufacture>(
            {
                name_manufacture: new FormControl('', [Validators.required]),
                code: new FormControl(''),
                url: new FormControl(''),
                support_url: new FormControl(''),
                pic: new FormControl(''),
                phone: new FormControl(''),
                email: new FormControl(''),
                logo: new FormControl(''),
                country: new FormControl(''),
                address: new FormControl(''),
                id_user: new FormControl(''),
                id_manufacture: new FormControl(''),
                created_at: new FormControl(''),
                updated_at: new FormControl(''),
            })
    }
    simpan() {
        this.spinner.show()
        if (this.dataForm.invalid) {
            this.pesan.pesanWarningForm('Form Tidak Valid')
            this.spinner.hide()
            return
        }

        this.data = this.dataForm.value
        this.vendorManufactureService.putVendorManufacturesId({id: this.id_manufacture, body: this.data}).subscribe(
            data => {
                this.goToBack()
                this.spinner.hide()
            },
            err => {
                this.pesan.pesanWarningForm('Gagal di simpan')
                this.spinner.hide()
            },
            () => {
                console.log('HTTP request completed.')
            },
        )
    }

    goToBack() {
        this.router.navigate(['/vendor/manufacture/list'])
    }
}
